import React, { Component } from 'react';
import Syllabusaccordion from './Syllabusaccordion'

export default class Course extends Component {
    constructor(){
        super();
        this.state={
            pageData : [
            {
                title : 'Introduction',
                topics : [
                    {
                        title : "What is Physics",
                        type : "text",
                        code : 'abc',
                        finished : true
                    },
                    {
                        title : "What is Physics",
                        type : "video",
                        code : 'def',
                        finished : true
                    },
                    {
                        title : "Scope of the course",
                        type : "text",
                        code : 'def',
                        finished : true
                    }
                ]
            },
            {
                title : 'Laws of motion',
                topics : [
                    {
                        title : "First Law",
                        type : "video",
                        code : 'def',
                        finished : true
                    },
                    {
                        title : "Second Law",
                        type : "video",
                        code : 'def',
                        finished : false
                    },
                    {
                        title : "Third Law",
                        type : "video",
                        code : 'def',
                        finished : false
                    },
                    {
                        title : "Numericals",
                        type : "text",
                        code : 'def',
                        finished : false
                    }
                ]
            }
        
        ]
        }
    }
    render() {
        return (
            <div className="row coursePage">
                <div className="heading ms-fontSize-su">
                    Newtonian Physics
                </div>
                <div className="description">
                    All trademarks are the property of their respective owners. Usage of Fabric assets, such as fonts and icons, is subject to the assets license agreement. All trademarks are the property of their respective owners. Usage of Fabric assets, such as fonts and icons, is subject to the assets license agreement.
                </div>
                <Syllabusaccordion data={this.state.pageData}/>
            </div>
        )
    }
}


