import React, { Component } from 'react';
import Courselist from './Courselist';
import style from './style/common.css';
import grid from './style/foundation.css';
import { Link } from 'office-ui-fabric-react/lib/Link';
import { TextField } from 'office-ui-fabric-react/lib/TextField';
import { IconButton, IButtonProps } from 'office-ui-fabric-react/lib/Button';

import photo from './assets/pp.jpg'

const examplePersona = {
        imageUrl: '',
        imageInitials: 'AL',
    };

export default class User extends Component {
    constructor() {
        super();
        this.state = {
            userDetail : {
                name : 'John Doe',
                email : 'abc@def.com',
                phone : '+91-966878789'
            },
            editMode : false
        }
        this.editDetals = () => {
            console.log('edit')
            let newState = Object.assign({}, this.state)
            newState.editMode = true;
            this.setState(newState);
        }
    }

    render() {
        return (
        <div className="row">
            <div className="userinfo column large-4 large-uncentered small-10 small-centered">
                <div className="image">
                    <img src={photo} className="ms-borderColor-themePrimary" alt=""/>
                </div>
                <p className="ms-fontWeight-regular ms-fontSize-l name">{this.state.userDetail.name}</p>
                <p className="ms-fontWeight-semilight ms-fontSize-sPlus email">{this.state.userDetail.email}</p>
                <p className="ms-fontWeight-semilight ms-fontSize-sPlus phone">{this.state.userDetail.phone}</p>
                <Link
                className="editLink"
                onClick = {()=> this.editDetals()}
                >Edit</Link>
            </div>
            <Courselist />
        </div>
        )
    }
}


