import React, { Component } from 'react';
import Bloglist from './Bloglist';
import { PrimaryButton, IButtonProps } from 'office-ui-fabric-react/lib/Button';
import {
  ProgressIndicator
} from 'office-ui-fabric-react/lib/ProgressIndicator';
import style from './style/common.css'

export default class Courselist extends Component {
    constructor() {
        super();
    }

    render() {
        return (
            <div className="courselist column large-8 small-10 small-push-1 large-push-0 end">
                <div className="course">
                    <div className="courseDetail row ms-bgColor-neutralLight">
                        <div className="column large-8">
                            <p className="ms-fontSize-xl ms-fontWeight-regular chapter">Newtonian Physics</p>
                            <p className="ms-fontSize-l ms-fontWeight-regular continue">Continue from : Chapter 6</p>
                        </div>
                        <div className="ms-u-textAlignCenter action">
                            <PrimaryButton
                                text='View Course'
                                onClick={() => window.location='/course'}
                            />
                        </div>
                    </div>
                    <div className="row">
                            <ProgressIndicator
                                percentComplete={ 0.80 } /> 
                    </div>
                </div>
                <div className="course">
                    <div className="courseDetail row ms-bgColor-neutralLight">
                        <div className="column large-8">
                            <p className="ms-fontSize-xl ms-fontWeight-regular chapter">Newtonian Physics</p>
                            <p className="ms-fontSize-l ms-fontWeight-regular continue">Continue from : Chapter 6</p>
                        </div>
                        <div className="ms-u-textAlignCenter action">
                            <PrimaryButton
                                text='View Course'
                                onClick={() => window.location='/course'}
                            />
                        </div>
                    </div>
                    <div className="row">
                            <ProgressIndicator
                                percentComplete={ 0.60 } /> 
                    </div>
                </div>
                <div className="course">
                    <div className="courseDetail row ms-bgColor-neutralLight">
                        <div className="column large-8">
                            <p className="ms-fontSize-xl ms-fontWeight-regular chapter">Newtonian Physics</p>
                            <p className="ms-fontSize-l ms-fontWeight-regular continue">Continue from : Chapter 6</p>
                        </div>
                        <div className="ms-u-textAlignCenter action">
                            <PrimaryButton
                                text='View Course'
                                onClick={() => window.location='/course'}
                            />
                        </div>
                    </div>
                    <div className="row">
                            <ProgressIndicator
                                percentComplete={ 0.02 } /> 
                    </div>
                </div>
                <Bloglist/>
            </div>
        )
    }
}


