import React, { Component } from 'react';
import { Link } from 'office-ui-fabric-react/lib/Link';
import Linktopics from './Linktopics'


export default class Syllabusaccordion extends Component {
    constructor(props){
        super(props);
        this.state = {
            openArray : new Array(this.props.data.length)
        }

        this.toggleShow = (index) => {
            console.log('toggle', index);
            this.newState = Object.assign({}, this.state);
            for(let i of this.newState.openArray.keys()) {
               if(i === index) {
                    this.newState.openArray[i] = true;
                }   else {
                    this.newState.openArray[i] = false;
                } 
            }
            this.setState(this.newState)
        }
    }
    render() {
        var data = JSON.stringify(this.props.data)
        return (
            <div className="syllabusaccordion">
                {[...this.props.data].map((chapter, i)=>
                    <div className={`chapter ${this.state.openArray[i]?"open":"close"}`}
                        key={i}
                        >
                        <p className="title ms-bgColor-neutralLighter ms-bgColor-neutralLight--hover"
                        onClick={()=>this.toggleShow(i)}
                        >{chapter.title}</p>
                        {/*<div className="allTopics">
                            {[...chapter.topics].map((topic, i)=>
                                <Link 
                                key={i}
                                className={ `topic type-${topic.type} ${topic.finished?"finished":""}`}
                                href="/course/chapter"
                                >{topic.title}</Link>
                            )}
                        </div>*/}
                        <Linktopics data={chapter.topics}/>
                    </div>
                )}
            </div>
        )
    }
}


