import React, { Component } from 'react';
import { IconButton, IButtonProps } from 'office-ui-fabric-react/lib/Button';
import Notificationpanel from './Notificationpanel'
import style from './style/header.css'

export default class Header extends Component {
    constructor() {
        super();
        this.state = {
            showPanel: false
        };
        this.onClosePanel = () => {
            console.log('close panel');
            this.setState({ showPanel: false });
        }

        this.onShowPanel = () => {
            console.log('show panel');
            this.setState({ showPanel: true });
        }
    }
    
    render() {
        return(
            <div className="header ms-bgColor-themePrimary">
                <div className="content row">
                    <div className="logo ms-fontSize-xl ms-fontColor-white column large-1 medium-1 small-1">Saarthi</div>
                    <div className='notification ms-fontColor-white column large-1 medium-1 small-1'>
                        <IconButton
                        className='icon'
                        iconProps={ { iconName: 'FavoriteList' } }
                        ariaLabel='Notification' 
                        onClick={this.onShowPanel}/>
                        <Notificationpanel close={this.onClosePanel} status={this.state.showPanel}/>
                    </div>
                </div>
            </div>
        )
    }
}


