import React, { Component } from 'react';
import { Panel, PanelType } from 'office-ui-fabric-react/lib/Panel';
import style from './style/header.css'
import {
  CompoundButton,
  IButtonProps
} from 'office-ui-fabric-react/lib/Button';
import { Label } from 'office-ui-fabric-react/lib/Label';

export default class Notificationpanel extends Component {
    constructor() {
        super();
    }
    render() {
        //debugger;
        return(
            <Panel
                isOpen={this.props.status}
                type={PanelType.smallFixedFar}
                onDismiss={this.props.close}
                headerText='Notifications'
                onRenderFooterContent = {() => {
                    return (
                        <div class="notificationList">
                            <div className='notif'>
                                <Label><i className="ms-Icon ms-Icon--Mail" aria-hidden="true"></i> Course1 </Label>
                                <CompoundButton
                                description='The class may be reschedules'
                                onClick = {() => alert('notification clicked')}
                                >
                                Contact for more info
                                </CompoundButton>
                            </div>
                            <div className='notif'>
                                <Label><i className="ms-Icon ms-Icon--Mail" aria-hidden="true"></i> Course2 </Label>
                                <CompoundButton
                                description='The class may be reschedules'
                                onClick = {() => alert('notification clicked')}
                                >
                                Contact for more info
                                </CompoundButton>
                            </div>
                        </div>
                    )
                }}
            />
        )
    }
}


