import React, { Component } from 'react';
import { Link } from 'office-ui-fabric-react/lib/Link';

export default class Linktopics extends Component {
    render() {
        return (
            <div className={`allTopics ${this.props.className}`}>
                {[...this.props.data].map((topic, i)=>
                    <Link 
                    key={i}
                    className={ `topic ${topic.active?"active":""} type-${topic.type} ${topic.finished?"finished":""}`}
                    href="/course/chapter"
                    >{topic.title}</Link>
                )}
            </div>
        )
    }
}


