import React, { Component } from 'react';
import blog from './assets/blog.jpg'
import style from './style/common.css'

export default class Bloglist extends Component {
    constructor() {
        super();
    }

    render() {
        return (
            <div className="bloglist row">
                <div className="large-up-3 small-up-2">
                    <div className="column column-block blogLink">
                        <img src={blog} alt=""/>
                        <p className="title ms-fontWeight-semilight ms-fontColor-white ms-bgColor-black ms-bgColor-themePrimary--hover">New Blog Title, Nice!</p>
                    </div>
                    <div className="column column-block blogLink">
                        <img src={blog} alt=""/>
                        <p className="title ms-fontWeight-semilight ms-fontColor-white ms-bgColor-black ms-bgColor-themePrimary--hover">New Blog Title, Nice!</p>
                    </div>
                    <div className="column column-block blogLink">
                        <img src={blog} alt=""/>
                        <p className="title ms-fontWeight-semilight ms-fontColor-white ms-bgColor-black ms-bgColor-themePrimary--hover">New Blog Title, Nice!</p>
                    </div>
                    <div className="column column-block blogLink">
                        <img src={blog} alt=""/>
                        <p className="title ms-fontWeight-semilight ms-fontColor-white ms-bgColor-black ms-bgColor-themePrimary--hover">New Blog Title, Nice!</p>
                    </div>
                    <div className="column column-block blogLink">
                        <img src={blog} alt=""/>
                        <p className="title ms-fontWeight-semilight ms-fontColor-white ms-bgColor-black ms-bgColor-themePrimary--hover">New Blog Title, Nice!</p>
                    </div>
                </div>
            </div>
        )
    }
}


